.box-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 273px);

  .box-1 {
    display: flex;
    justify-content: space-around;
  }

  .box-2 {
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
  }

  .img {
    width: 200px;
    height: 200px;
    border-radius: 8px;
    box-shadow: 0 8px 8px 0 #773da1;
    margin-right: 100px;
    margin-left: 100px;
    margin-bottom: 20px;
  }
}
